import * as React from "react"
import {
    Flex
    , Text
    , Icon
    , Link
    , Menu
    , MenuButton
    , MenuList
} from "@chakra-ui/react"
import AppNavHoverBox from "./AppNavHoverBox"

const AppNavItem = ({ navSize, title, icon, active, description }) => {
    return (
        <Flex
            mt='30'
            flexDir='column'
            w='100%'
            alignItems={navSize === "small" ? 'center' : 'flex-start'}
        >
            <Menu placement="right">
                <Link
                    backgroundColor={active && '#AEC8CA'}
                    p={3}
                    borderRadius={8}
                    _hover={{ textDecor: "none", backgroundColor: "purple.400" }}
                    w={navSize === 'large' && '100%'}
                >
                    <MenuButton w='100%'>
                        <Flex>
                            <Icon as={icon} fontSize='xl' color={active ? '#82AAAD' : 'gray.900'} />
                            <Text fontWeight='bold' ml={5} display={navSize === 'small' ? 'none' : 'flex'}>{title}</Text>
                        </Flex>
                    </MenuButton>
                </Link>
                <MenuList
                    py={0}
                    border='none'
                    w={200}
                    h={200}
                    ml={5} 
                >
                    <AppNavHoverBox title={title} icon={icon} description={description} />
                </MenuList>
            </Menu>
        </Flex>
    )
}

export default AppNavItem
