import * as React from "react"
import { Flex, Divider, Heading, Text, Avatar, IconButton } from "@chakra-ui/react"
import { FiMenu, FiHome, FiCalendar, FiUser, FiDollarSign, FiBriefcase, FiSettings } from 'react-icons/fi'
import profile from '../images/profile.png'
import AppNavItem from "./AppNavItem"

const AppSidebar = () => {

    const [navSize, changeNavSize] = React.useState('large')
    return (
        <Flex
            bgColor='gray.300'
            pos='fixed'
            left='2'
            h='96vh'
            marginTop='2.5vh'
            boxShadow='0 4px 12 px 0 rgba(0, 0, 0, 0.05)'
            borderRadius={(navSize === 'small' ? '15px' : '30px')}
            w={navSize === 'small' ? '50px' : '180px'}
            flexDir='column'
            justifyContent="space-between"
        >
            <Flex
                p='5%'
                flexDir='column'
                alignItems={navSize === 'small' ? 'center' : 'flex-start'}
                as='nav'
            >
                <IconButton
                    background='none' mt={3}
                    _hover={{ background: 'gray.200' }}
                    icon={<FiMenu />}
                    onClick={() => { (navSize === "small" ? changeNavSize("large") : changeNavSize('small')) }}
                />

                <AppNavItem navSize={navSize} title='About' icon={FiHome} description='This is the description for the About Hover' />
                <AppNavItem navSize={navSize} title='Software' icon={FiHome} />
                <AppNavItem navSize={navSize} title='Hardware' icon={FiHome} />
                <AppNavItem navSize={navSize} title='Projects' icon={FiHome} />
                <AppNavItem navSize={navSize} title='Resume' icon={FiHome} />
                <AppNavItem navSize={navSize} title='Media' icon={FiHome} />


            </Flex>

            <Flex
                p='5%'
                flexDir='column'
                w='100%'
                align={navSize === 'small' ? 'center' : 'flex-start'}
                mb={4}
            >
                <Divider display={navSize === 'small' ? 'none' : 'flex'} />
                <Flex mt={4} align='center'>
                    <Avatar size='sm' src={profile} />
                    <Flex flexDir='column' ml={4} display={navSize === 'small' ? 'none' : 'flex'}>
                        <Heading as='h3' size='sm'>David Torres</Heading>
                        <Text>Admin</Text>
                    </Flex>
                </Flex>
            </Flex>


        </Flex>
    )
}

export default AppSidebar
